import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { CryptoUtil } from '../../../core/utils/crypto.util';

export interface FormUser {
  id: string;
  username: string;
  role: 'admin' | 'user';
}

@Injectable({
  providedIn: 'root'
})
export class FormAuthService {
  private currentUserSubject = new BehaviorSubject<FormUser | null>(null);
  private isFormLoggedInSubject = new BehaviorSubject<boolean>(false);
  
  public currentUser$: Observable<FormUser | null> = this.currentUserSubject.asObservable();
  public isFormLoggedIn$: Observable<boolean> = this.isFormLoggedInSubject.asObservable();

  // environmentì—ì„œ ì•”í˜¸í™”ëœ ì¸ì¦ ì •ë³´ë¥¼ ë³µí˜¸í™”í•˜ì—¬ ì‚¬ìš©
  private readonly VALID_USERNAME = CryptoUtil.decrypt(environment.auth.username);
  private readonly VALID_PASSWORD = CryptoUtil.decrypt(environment.auth.password);

  constructor() {
    // Form ë¡œê·¸ì¸ ìƒíƒœ ë³µì›
    const savedUser = localStorage.getItem('formUser');
    const savedToken = localStorage.getItem('formToken');
    
    if (savedUser && savedToken) {
      try {
        this.currentUserSubject.next(JSON.parse(savedUser));
        this.isFormLoggedInSubject.next(true);
      } catch (error) {
        this.clearFormAuth();
      }
    }
  }

  get currentUser(): FormUser | null {
    return this.currentUserSubject.value;
  }

  get isFormLoggedIn(): boolean {
    return this.isFormLoggedInSubject.value;
  }

  /**
   * Form ì‹œìŠ¤í…œ ë¡œê·¸ì¸ (í•˜ë“œì½”ë”©)
   */
  async formLogin(username: string, password: string): Promise<{ success: boolean; message?: string }> {
    try {
      // ë¡œë”© ì‹œë®¬ë ˆì´ì…˜
      await new Promise(resolve => setTimeout(resolve, 500));
      
      // í•˜ë“œì½”ë”©ëœ ì¸ì¦ í™•ì¸
      if (username === this.VALID_USERNAME && password === this.VALID_PASSWORD) {
        const user: FormUser = {
          id: '1',
          username: username,
          role: 'admin'
        };

        const token = 'form_token_' + Date.now();

        this.currentUserSubject.next(user);
        this.isFormLoggedInSubject.next(true);
        localStorage.setItem('formUser', JSON.stringify(user));
        localStorage.setItem('formToken', token);
        
        return { success: true };
      } else {
        return { success: false, message: 'ì•„ì´ë”” ë˜ëŠ” ë¹„ë°€ë²ˆí˜¸ê°€ ì˜¬ë°”ë¥´ì§€ ì•ŠìŠµë‹ˆë‹¤.' };
      }
    } catch (error) {
      return { success: false, message: 'ë¡œê·¸ì¸ ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.' };
    }
  }

  /**
   * Form ë¡œê·¸ì•„ì›ƒ
   */
  formLogout(): void {
    this.currentUserSubject.next(null);
    this.isFormLoggedInSubject.next(false);
    this.clearFormAuth();
  }

  /**
   * Form í† í° ê°€ì ¸ì˜¤ê¸°
   */
  getFormToken(): string | null {
    return localStorage.getItem('formToken');
  }

  /**
   * Form ì¸ì¦ ì •ë³´ ì´ˆê¸°í™”
   */
  private clearFormAuth(): void {
    localStorage.removeItem('formUser');
    localStorage.removeItem('formToken');
  }
}
