import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormTestComponent } from './pages/form-test/form-test.component';
import { DynamicFormBuilderComponent } from './pages/dynamic-form-builder/dynamic-form-builder.component';
import { FormLogin } from './pages/form-login/form-login';
import { FormAuthGuard } from '../../guards/form-auth.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'builder',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: FormLogin
  },
  {
    path: 'builder',
    component: DynamicFormBuilderComponent,
    canActivate: [FormAuthGuard]
  },
  {
    path: 'test',
    component: FormTestComponent,
    canActivate: [FormAuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FormRoutingModule { }
