import { Component, Input } from '@angular/core';
import { FormAbstract } from '../../../classes/abstracts/form.abstract';
import { Observable } from 'rxjs';
import { IGroup, IControl } from '../../../classes/interfaces/form.interface';

@Component({
  selector: 'app-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.scss'],
  standalone: false
})
export class SelectComponent extends FormAbstract {

  @Input() groupOptions: Observable<IGroup[]>[] = [];
  @Input() formData: IControl[] = [];

  public getSelectIndex(name: string): number {
    const selects = this.formData.reduce((indices, item, index) => {
      if (item.category === 'select') {
        indices.push({
          name: item.name,
          index
        });
      }
      return indices;
    }, [] as { name: string; index: number; }[]);

    return selects.findIndex(item => item.name === name);
  }

}
